import React from 'react';
import ReactDOM from 'react-dom';
import {Component} from 'react';
import {Fragment} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Header from './component/Header';
import Main from './component/Main';
import Footer from './component/Footer';
import './index.css';
import * as serviceWorker from './serviceWorker';
class Place extends Component {
    render() {
        return (<Fragment>
            <Header/>
        </Fragment>)
    }
}
ReactDOM.render(<Place/>, document.getElementById('header'));
class KJ extends Component {
    render() {
        return (<Fragment>
            <Main/>
        </Fragment>)
    }
}
ReactDOM.render(<KJ/>, document.getElementById('main'));
class Ft extends Component {
    render() {
        return (<Fragment>
            <Footer/>
        </Fragment>)
    }
}
ReactDOM.render(<Ft/>, document.getElementById('footer'));
serviceWorker.unregister();
